import React, { useState, useEffect } from "react";
import { useStripe, useElements, CardElement } from "@stripe/react-stripe-js";
import CardSection from "./CardSection";
import "../CSS/register.css";
import axios from "axios";
import { TextField, Button, Divider } from "@material-ui/core";
import pbs from "../../assets/pbsB.png";
import PaymentCard from "./PaymentCard";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { set } from "js-cookie";

export default function CheckoutForm(props) {
  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#2c1951",
      },
      secondary: {
        main: "#f2f2f7",
      },
    },
  });
  let [currentSelected, setCurrentSelected] = useState(0);

  const stripe = useStripe();
  const elements = useElements();
  const [text, setText] = useState("");
  const [promo, setPromo] = useState("");
  const [promoID, setPromoID] = useState("");
  const [percent, setPercent] = useState(0);
  const [promoError, setPromoError] = useState("");
  const handleSubmit = async (event) => {
    //console.log(props.location.state.customerID);
    // We don't want to let default form submission happen here,
    // which would refresh the page.

    event.preventDefault();

    if (!stripe || !elements) {
      // Stripe.js has not yet loaded.
      // Make  sure to disable form submission until Stripe.js has loaded.
      return;
    }

    const card = elements.getElement(CardElement);
    const result = await stripe.createToken(card);

    if (result.error) {
      // Show error to your customer.
      console.log(result.error.message);
    } else {
      // Send the token to your server.
      // This function does not exist yet; we will define it in the next step.
      addDetails(props.location.state.customerID, result.token.id);
      console.log(result.token.id);
    }
  };
  const handlePromo = async (e) => {
    e.preventDefault();
    console.log(promo);
    await axios
      .post("https://frca-reveal-backend.herokuapp.com/promo", {
        code: promo,
      })
      .then((response) => {
        setPromoID(response.data.id);
        setPercent(response.data.percent);
        setPromoError("Promo Code Applied");
      })
      .catch((err) => {
        setPromoError("Invalid Promo Code");
      });
  };

  const addDetails = async (ID, token) => {
    setText("Loading...");
    await axios
      .post("https://frca-reveal-backend.herokuapp.com/createMethod", {
        token: token,
        customerID: ID,
      })
      .then(async (response) => {
        console.log(response);
        await axios
          .post("https://frca-reveal-backend.herokuapp.com/createPay", {
            customerID: ID,
            paymentID: response.data.id,
            subType: parseInt(currentSelected),
            discount: percent,
          })
          .then((resp) => {
            if (response.data.statusCode >= 400) {
              setText("Card Error Try Again");
            } else {
              props.history.push("/success");
            }
            console.log(resp);
          })
          .catch((err) => {
            setText("Please Select a package");
          });
      })
      .catch((error) => {
        setText("Card Error");
      });
  };
  function listenCookieChange(callback, interval = 1000) {
    let lastCookie = document.cookie;
    setInterval(() => {
      let cookie = document.cookie;
      if (cookie !== lastCookie) {
        try {
          callback({ oldValue: lastCookie, newValue: cookie });
        } finally {
          lastCookie = cookie;
        }
      }
    }, interval);
  }

  listenCookieChange(({ oldValue, newValue }) => {
    setCurrentSelected(
      document.cookie
        .split("; ")
        .find((row) => row.startsWith("subType"))
        .split("=")[1]
    );
  }, 1000);

  return (
    <div className="fullBody">
      <div className="container">
        <div className="payment-cards">
          <PaymentCard
            months={1}
            price={"£" + parseFloat(14 * (1 - percent / 100)).toFixed(2)}
          />
          <PaymentCard
            months={2}
            price={"£" + parseFloat(22 * (1 - percent / 100)).toFixed(2)}
          />
          <PaymentCard
            months={3}
            price={"£" + parseFloat(30 * (1 - percent / 100)).toFixed(2)}
          />
          <PaymentCard
            months={6}
            price={"£" + parseFloat(40 * (1 - percent / 100)).toFixed(2)}
          />
        </div>

        <form onSubmit={handleSubmit} className="formContainer">
          <Divider />
          <b>
            {currentSelected
              ? "You have selected the " + currentSelected + " month package"
              : "Please select a package"}{" "}
          </b>
          <br />
          <br />
          <TextField
            id="promo"
            label="Promo Code"
            variant="outlined"
            margin="normal"
            onChange={(e) => {
              setPromo(e.target.value);
            }}
          />
          <h5>{promoError}</h5>
          <ThemeProvider theme={theme}>
            <div className="inlineButton">
              <Button
                variant="contained"
                color="secondary"
                onClick={handlePromo}
                margin="small"
                size="small"
                fullWidth
              >
                Apply
              </Button>
            </div>
          </ThemeProvider>

          <br />
          <CardSection />

          <button className="button" disabled={!stripe}>
            CONFIRM
          </button>
        </form>
        {text}
        <div className="pbsDiv">
          <a href="https://stripe.com/">
            <img src={pbs} className="pbs" alt="Powered by Stripe"></img>
          </a>
        </div>
      </div>
    </div>
  );
}
