import React, { useEffect } from "react";
import "./App.css";
import Home from "./Screens/Home";
import Register from "./Screens/Register";
import Payment from "./Screens/Payment";
import Success from "./Screens/Success";
import About from "./Screens/About";
import { HashRouter, Switch, Route, Link, Router } from "react-router-dom";
import Forgot from "./Screens/Forgot";
import Privacy from "./Screens/Privacy";
import logo from "./assets/logo/logo.png";
import Login from "./Screens/Login";
import Account from "./Screens/Account";
import { TextField, Button, Divider } from "@material-ui/core";

function Nav() {
  useEffect(() => {
    console.log("reloaded");
  });
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }
  return (
    <div className="navContainer">
      <img src={logo} className="logo" alt="logo"></img>

      <div className="navLinkCont">
        <ul>
          <li>
            <Link to="/" className="navLink">
              HOME
            </Link>
          </li>
          <li>
            <Link to="/about" className="navLink">
              ABOUT
            </Link>
          </li>
          <li>
            <Link
              to={getCookie("accessToken") ? "/account" : "/login"}
              className="navLink"
            >
              {getCookie("accessToken") ? "ACCOUNT" : "LOGIN"}
            </Link>
          </li>
          <li>
            <Link to="/register" className="navLink">
              REGISTER
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
}
function Contact() {
  return (
    <div className="contactContainer">
      <Divider light={false} variant="fullWidth" />
      <br />
      <Link to="/privacy" className="contactLink">
        PRIVACY
      </Link>
      <br />
      <a href="mailto:frca.reveal@gmail.com" className="contactLink">
        CONTACT
      </a>
    </div>
  );
}

function App() {
  return (
    <HashRouter basename="/">
      <Nav />
      <Route path="/" exact component={Home} />
      <Route path="/login" exact component={Login} />
      <Route path="/register" exact component={Register} />
      <Route path="/payment" exact component={Payment} />
      <Route path="/about" exact component={About} />
      <Route path="/success" exact component={Success} />
      <Route path="/account" exact component={Account} />
      <Route path="/privacy" exact component={Privacy} />
      <Route path="/forgot" exact component={Forgot} />
      <Contact />
    </HashRouter>
  );
}

export default App;
